.stat-tracker-card {
    width: 100px;
    position: absolute;
    right: -50px;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
    padding: 8px;
}

.stats-title {
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
}

.stat {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}

.stat-value {
    margin-left: 8px;
    font-size: 1.1rem;
    color: #333;
}